.wrapper {
  display: flex;
  align-items: center;
  padding: 217px 48px;

  @media screen and (max-width: 1024px) {
    padding: 104px 48px;
  }

  @media screen and (max-width: 768px) {
    padding: 68px 36px;
  }

  @media screen and (max-width: 320px) {
    padding: 46px 20px;
  }
}

